
import { defineComponent } from 'vue';
import { useRouter } from 'vue-router'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    setup() {
        const router = useRouter();
        const qf = new QuickFetch();
        const user_data = JSON.parse(localStorage.nhc_admin_data);
        function logout(){
            localStorage.removeItem('nhc_admin_token');
            localStorage.removeItem('nhc_admin_data');
            router.push('/login');
        }
        return {
            logout,
            user_data,
        }
    }
});
